import React, { useState } from 'react';
import { Card, CardHeader, Row } from 'reactstrap';

import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import SingleLightbox from '../../../../components/application/SingleLightbox';

import NavItems from './components/NavItems';
import TabPanes from './components/TabPanes';

import useFetch from '../../../../hooks/use-fetch';
import HackDetails from './components/HackDetails';
import AppError from '../../../../helpers/AppError';

const Details = ({ match }) => {
  const { id } = match.params;

  // if (!id) throw new AppError('Please Provide Hackathon ID');

  const [activeTab, setActiveTab] = useState('basics');

  const { loading, data, error } = useFetch(
    `/getdetails/${id}`,
    false,
    'getdetails'
  );

  if (loading) return <div className="loading" />;

  if (error) throw new AppError(error);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.details" match={match} />
        </Colxx>
      </Row>

      <Separator className="mb-5" />

      <Row>
        <Colxx xxs="12" className="mb-5">
          <Card>
            <SingleLightbox
              thumb="/assets/img/social/header.jpg"
              large="/assets/img/social/header.jpg"
              // thumb={data.hackathon.hackathon_cover_img}
              // large={data.hackathon.hackathon_cover_img}
              className="social-header card-img"
            />
          </Card>
        </Colxx>

        <Colxx xxs="12" sm="2" md="3" className="mb-4 col-left">
          <SingleLightbox
            thumb="/assets/img/profiles/l-10.jpg"
            large="/assets/img/profiles/10.jpg"
            // thumb={data.hackathon.hackathon_logo}
            // large={data.hackathon.hackathon_logo}
            className="img-thumbnail card-img social-profile-img"
          />
          <HackDetails hackInfo={data} />
        </Colxx>

        <Colxx xxs="12" sm="10" md="9" className="mb-4 col-right">
          <Card className="mb-4">
            <CardHeader>
              <NavItems activeTab={activeTab} setActiveTab={setActiveTab} />

              <TabPanes activeTab={activeTab} hackInfo={data} />
            </CardHeader>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

export default Details;
